import find from "./deps/framework/selector";

if (window.BrickJS == null) {
  window.BrickJS = {};
}

window.BrickJS = {
  ...window.BrickJS,
  $: find
};

(function Main (BrickJS) {
  const $hamburger = BrickJS.$("[data-js='hamburger']").first();
  const $header = BrickJS.$("[data-js='header']").first();

  if ($hamburger && $header) {
    const toggleMenu =
      (event) => {
        const isVisible = $header.classList.contains("active");

        if (isVisible) {
          document.body.removeEventListener("click", toggleMenu);
        } else {
          document.body.addEventListener("click", toggleMenu);
        }

        $header.classList.toggle("active");
        $hamburger.setAttribute("aria-expanded",
          String($header.classList.contains("active")));

        event.stopPropagation();
      };

    $hamburger.addEventListener("click", toggleMenu);
  }
})(window.BrickJS);
