const slice = [].slice;

class Collection extends Array {
  constructor (collection) {
    super(...slice.call(collection));
  }

  eq (index) {
    return this[index];
  }

  first () {
    return this.length == 0
      ? null
      : this[0];
  }

  last () {
    return this.length == 0
      ? 0
      : this[this.length - 1];
  }
}

const internalFind =
  (context, selector) => {
    return new Collection(
      (context || document.body)
        .querySelectorAll(":scope " + selector.trim())
    );
  };

/**
 * @name find
 * @param {HTMLElement?} context
 * @param {String} selector
 * @return {HTMLElement[]|Function}
 */
const find =
  (context, selector) => {
    if (selector == null) {
      // find(context);
      if (typeof context == "string") {
        // eg. find(".js-foo");
        selector = context;
        context = null;
      } else {
        // partial application, eg. find(document.body);
        return internalFind.bind(void 0, context);
      }
    }

    // find(context, selector);
    return internalFind(context, selector);
  };

export default find;
